import React, { Component } from 'react'
import { Layout, Menu, Icon } from 'antd'

const { Sider } = Layout

class Sidebar extends Component {
  state = {
    collapsed: true
  }

  onCollapse = (collapsed) => {
    this.setState({ collapsed })
  }

  render () {
    return (
      <Sider
        style={{ backgroundColor: '#fff' }}
        collapsible
        collapsed={this.state.collapsed}
        onCollapse={this.onCollapse}
      >
        <Menu>
          <Menu.Item key='calendar'>Calendar</Menu.Item>
          <Menu.Item key='notepad'>Notepad</Menu.Item>
          <Menu.Item key='chat_room'>Chat Room</Menu.Item>
          <Menu.Item key='this_week'>This Week</Menu.Item>
          <Menu.SubMenu title={<span><Icon type='laptop' />Blog</span>}>
            <Menu.Item key='articles'>Articles</Menu.Item>
            <Menu.Item key='videos'>Videos</Menu.Item>
            <Menu.Item key='inspiration'>Inspiration</Menu.Item>
            <Menu.Item key='diy'>DIY's</Menu.Item>
          </Menu.SubMenu>
          <Menu.Item key='my_profile'>My Profile</Menu.Item>
        </Menu>
      </Sider>
    )
  }
}

export default Sidebar
